.sublinks {
    color: black;
    margin: 10px;
}

.Link {
    text-decoration: none;
    color: inherit;
}

.panel {
    text-align: center;
}

.sublinks:hover {
    color: gray;
    cursor: pointer;
}

#sear {
    display: none;
}

.mobilenavrow {
    color: black;
}

#desknavbar {
    display: none;
}

.navbarr {
    margin-top: 5px;
}

.mobilenavbar {
    padding: 30px;
}

.black-text {
    margin-left: 10px;
}

@media only screen and (min-width: 1024px) {
    /* For dektop: */
    .barss {
        display: none;
    }
    #mobilenavbar {
        display: none;
    }
    #desknavbar {
        display: flex;
    }
    #search {
        width: 500px;
    }
    #sear {
        display: block;
    }
    .navbarr {
        margin-top: 4px;
    }
    .searchicon {
        width: 30px;
        height: 40px;
        margin-left: -5px;
        border-bottom: 1px solid black;
        padding-top: 12px;
        vertical-align: center;
        text-align: center;
        background-color: yellow;
    }
}

